// NOTES :: START

.notes {
  &__search {
    display: flex;
    position: relative;
    width: 100%;
    max-width: 255px;
    height: 40px;
    margin-right: 30px;

    .section__title--recent &,
    .section__title--messages &,
    .section__title--browse &,
    .section__title--forum & {
      margin-right: 0;
    }

    &--focus {
      input {
        border-color: #019552;
      }
      button {
        i {
          color: #019552;
        }
      }
      a {
        opacity: 1;
      }
    }

    &-input {
      outline: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: none;
      border-radius: 0;
      border-bottom: 1px solid #e2e6e9;
      background-color: transparent;
      padding: 0 30px;
      font-size: 12px;
      @include inputPlaceholder {
        color: #7F7F7F;
      }
    }

    &-btn {
      outline: none;
      cursor: pointer;
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 100%;
      padding: 0;
      border: none;
      border-radius: 0;
      background-color: transparent;
      text-align: left;

      i {
        font-size: 15px;
      }
    }

    &-clear {
      opacity: 0;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 0;
      right: 0;
      width: 30px;
      height: 100%;

      i {
        color: #9B9B9B;
        font-size: 13px;
      }
    }
  }

  &__box {
    position: relative;
    display: flex;
    width: 100%;
    min-height: 113px;
    margin-bottom: 20px;
    border-radius: 4px;
    background-color: $c-white;
    box-shadow: 0 3px 4px 0 rgba($c-black, 0.1);

    &--statistic {
      min-height: 140px;
      margin-bottom: 10px !important;
    }

    &--classesJoin {
      min-height: 140px;

      .notes__box-info {
        padding-bottom: 10px;
      }
    }

    &--sortCustom {
      cursor: pointer;

      .notes__box-drag {
        display: flex;
      }
    }

    &:last-of-type {
      margin-bottom: 50px;
    }

    &-wrapper {
      display: flex;
      flex-direction: column;
    }

    &-left {
      overflow: hidden;
      position: relative;
      width: 160px;
      background-color: #e8e8e8;

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    &-middle {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 10px 30px;
      width: 100%;
      max-width: calc(100% - (160px + 50px));

      .notes__box--classesJoin & {
        max-width: calc(100% - (160px + 179px));
      }
    }

    &-right {
      display: flex;
      align-items: center;
      width: 50px;

      .icon-fullscreen {
        color: #333333;
        font-size: 20px;
      }

      .notes__box--classesJoin & {
        width: 179px;

        .c-btn {
          &:hover {
            color: $c-white;
          }
        }
      }
    }

    &-title {
      padding-bottom: 8px;
      color: #333;
      letter-spacing: 0.21px;
      font-size: 16px;
      font-weight: 600;

      .notes__box--statistic & {
        padding-bottom: 15px;
      }
    }

    &-info {
      display: flex;
      align-items: center;

      .notes__box--statistic & {
        padding-bottom: 15px;
      }

      a {
        display: flex;
        align-items: center;
        margin-right: 20px;

        &:last-of-type {
          margin-right: 0;
        }

        i {
          margin-right: 12px;
          color: $c-0e1111;

          &.icon-profile {
            font-size: 15px;
          }
          &.icon-recent {
            font-size: 17px;
          }
          &.icon-select {
            font-size: 15px;
          }
          &.icon-like {
            font-size: 15px;
          }
          &.icon-save {
            font-size: 15px;
          }
        }

        span {
          color: #7f7f7f;
          font-size: 12px;
        }
      }
    }

    &-details {
      display: flex;

      p {
        padding-right: 22px;
        color: #7F7F7F;

        &:last-of-type {
          padding-right: 0;
        }
      }
    }

    &-drag {
      display: none;
      position: absolute;
      align-items: center;
      width: 36px;
      height: 100%;
      top: 50%;
      transform: translateY(-50%);
      left: -28px;

      i {
        color: $c-0e1111;
        font-size: 16px;
      }
    }
  }

  &__statistics {
    padding-bottom: 30px;

    &-title {
      min-height: 42px;
      letter-spacing: 0.21px;
      font-size: 16px;
      font-weight: 600;
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
    }

    &-item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 50%;
      min-height: 30px;

      &:nth-child(odd) {
        padding-right: 30px;
      }
      &:nth-child(even) {
        padding-left: 30px;
      }

      span {
        font-size: 14px;

        &:first-of-type {
          color: #555352;
          font-weight: 600;
        }

        &:last-of-type {
          color: rgba(#555352, 0.5);
        }
      }
    }
  }

  &__graph {
    padding-bottom: 30px;

    .c-modal--classesStat & {
      padding-bottom: 0;
    }

    &-wrapper {
      display: flex;
    }

    &-left,
    &-right {
      width: 50%;
    }

    &-left {
      padding-right: 25px;
    }

    &-right {
      padding-left: 35px;
    }

    &-title {
      min-height: 42px;
      margin-bottom: 20px;
      letter-spacing: 0.21px;
      font-size: 16px;
      font-weight: 600;
    }

    &-content {
      position: relative;
      min-height: 240px;
    }
  }

  &__rate {
    &-wrapper {
      display: flex;
    }

    &-left,
    &-right {
      width: 50%;
    }

    &-left {
      padding-right: 30px;
    }

    &-right {
      padding-left: 30px;
    }

    &-title {
      min-height: 42px;
      margin-bottom: 10px;
      letter-spacing: 0.21px;
      font-size: 16px;
      font-weight: 600;
    }

    &-star {
      display: flex;
      align-items: center;
      padding-bottom: 20px;

      a {
        display: flex;
        align-items: center;
        margin-left: 12px;
        margin-right: 12px;

        &:first-of-type {
          margin-left: 0;
        }

        i {
          font-size: 25px;
          @include transition(color);

          &.icon-star {
            color: #00BD70;
            transform: rotate3d(0, 1, 0, -180deg);
          }
          &.icon-star-fill {
            color: #00BD70;
          }
          &.icon-star-full {
            color: #9B9B9B;
          }
        }
      }
    }

    &-info {
      width: 100%;
      min-height: 103px;
      padding: 15px;
      border-radius: 4px;
      border: 1px solid #CBCECB;

      p {
        color: #333;
        line-height: 2;
        font-size: 12px;
      }
    }

    &-btn {
      margin-top: 45px;
      max-width: 210px;
      margin-left: auto;
    }
  }
}

// NOTES :: END